.item {
    width: 280px;
}

.item img{
    width: 280px;
    /* border-top-left-radius: 18px; */
    /* border-top-right-radius: 18px; */
    border-radius: 18px;
}

.item p {
    margin: 6px 0px;
    font-weight: 600;
}

.item-prices{
    display: flex;
    gap: 20px;
}

.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}

.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}

@media(max-width: 1280px){
    .item{
        width: 220px;
        font-size: 14px;
    }
    .item img{
        width: 220px;
    }
}
@media(max-width: 1024px){
    .item {
        width: 170px;
        font-size: 13px;
    }
    .item img {
        width: 170px;
    }
}
@media(max-width: 800px){
    .item {
        width: 160px;
        font-size: 12px;
    }
    .item img {
        width: 160px;
    }
    .item-price-old{
        font-size: 16px;
    }
}
@media(max-width: 500px){
    .item {
        width: 160px;
        font-size: 12px;
    }
    .item img {
        width: 170px;
    }
}